import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useCart } from '../contexts/CartContext';
import axios from 'axios';

const ProductDetail = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { addToCart } = useCart();
  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [quantity, setQuantity] = useState(1);

  useEffect(() => {
    fetchProduct();
  }, [id]);

  const fetchProduct = async () => {
    try {
      const response = await axios.get(`/api/products/${id}`);
      setProduct(response.data);
    } catch (error) {
      console.error('Error fetching product:', error);
      if (error.response?.status === 404) {
        navigate('/products');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleAddToCart = () => {
    if (product) {
      addToCart({
        ...product,
        quantity: quantity
      });
      alert(' thm sn phm vo gi hng!');
    }
  };

  if (loading) {
    return (
      <div className="container my-5">
        <div className="text-center py-5">
          <div className="spinner-border spinner-border-lg" role="status">
            <span className="visually-hidden">ang ti...</span>
          </div>
        </div>
      </div>
    );
  }

  if (!product) {
    return (
      <div className="container my-5">
        <div className="text-center py-5">
          <h3>Khng tm thy sn phm</h3>
          <button 
            className="btn btn-primary mt-3"
            onClick={() => navigate('/products')}
          >
            Quay li danh sch sn phm
          </button>
        </div>
      </div>
    );
  }

  const currentPrice = product.sale_price || product.price;
  const originalPrice = product.price;
  const discountPercentage = product.sale_price 
    ? Math.round(((originalPrice - product.sale_price) / originalPrice) * 100)
    : 0;

  return (
    <div className="container my-5">
      {/* Breadcrumb */}
      <nav aria-label="breadcrumb" className="mb-4">
        <ol className="breadcrumb">
          <li className="breadcrumb-item">
            <button 
              className="btn btn-link p-0"
              onClick={() => navigate('/')}
            >
              Trang ch
            </button>
          </li>
          <li className="breadcrumb-item">
            <button 
              className="btn btn-link p-0"
              onClick={() => navigate('/products')}
            >
              Sn phm
            </button>
          </li>
          <li className="breadcrumb-item active">{product.name}</li>
        </ol>
      </nav>

      <div className="row g-5">
        {/* Product Image */}
        <div className="col-lg-5">
          <div className="position-relative">
            <img 
              src={product.image_url || 'https://via.placeholder.com/600x400/f8f9fa/6c757d?text=No+Image'} 
              alt={product.name}
              className="img-fluid rounded shadow-lg w-100"
              style={{ maxHeight: '500px', objectFit: 'cover' }}
            />
            {product.stock_quantity === 0 && (
              <div className="position-absolute top-0 start-0 m-3">
                <span className="badge bg-danger fs-6 p-2" style={{fontWeight:600}}>Ht hng</span>
              </div>
            )}
            {discountPercentage > 0 && (
              <div className="position-absolute top-0 end-0 m-3">
                <span className="badge bg-warning text-dark fs-6 p-2">
                  SALE {discountPercentage}%
                </span>
              </div>
            )}
          </div>
        </div>

        {/* Product Info */}
        <div className="col-lg-7">
          <h1 className="fw-bold text-dark mb-2" style={{fontSize:'2rem'}}>{product.name}</h1>
          <div className="mb-2">
            <span className="badge bg-primary me-2">{product.category_name}</span>
            {product.brand_name && (
              <span className="badge bg-secondary">{product.brand_name}</span>
            )}
          </div>
          <div className="mb-3">
            <span className="fw-bold text-danger" style={{fontSize:'2rem'}}>
              {new Intl.NumberFormat('vi-VN', { 
                style: 'currency', 
                currency: 'VND' 
              }).format(currentPrice)}
            </span>
            {product.sale_price && (
              <span className="fs-5 text-decoration-line-through text-muted ms-3">
                {new Intl.NumberFormat('vi-VN', { 
                  style: 'currency', 
                  currency: 'VND' 
                }).format(originalPrice)}
              </span>
            )}
          </div>
          <div className="mb-3">
            {product.stock_quantity > 0 ? (
              <span className="text-success fw-bold">Cn hng ({product.stock_quantity})</span>
            ) : (
              <span className="text-danger fw-bold">Ht hng</span>
            )}
          </div>
          {product.short_description && (
            <div className="mb-3">
              <strong>c im:</strong>
              <div className="text-muted">{product.short_description}</div>
            </div>
          )}
          {/* Add to Cart */}
          {product.stock_quantity > 0 && (
            <div className="mb-4">
              <div className="row g-3 align-items-end">
                <div className="col-auto">
                  <label className="form-label fw-medium">S lng:</label>
                  <div className="input-group" style={{width: '150px'}}>
                    <button 
                      className="btn btn-outline-secondary"
                      onClick={() => setQuantity(Math.max(1, quantity - 1))}
                    >
                      <i className="bi bi-dash"></i>
                    </button>
                    <input 
                      type="number" 
                      className="form-control text-center"
                      value={quantity}
                      onChange={(e) => setQuantity(Math.max(1, Math.min(product.stock_quantity, parseInt(e.target.value) || 1)))}
                      min="1"
                      max={product.stock_quantity}
                    />
                    <button 
                      className="btn btn-outline-secondary"
                      onClick={() => setQuantity(Math.min(product.stock_quantity, quantity + 1))}
                    >
                      <i className="bi bi-plus"></i>
                    </button>
                  </div>
                </div>
                <div className="col">
                  <button 
                    className="btn btn-dark btn-lg px-4"
                    onClick={handleAddToCart}
                  >
                    <i className="bi bi-cart-plus me-2"></i>
                    Thm vo gi hng
                  </button>
                </div>
              </div>
            </div>
          )}
          {/* Info Table */}
          <div className="mb-4">
            <table className="table table-bordered">
              <tbody>
                <tr>
                  <th style={{width:'160px'}}>M sn phm</th>
                  <td>{product.sku}</td>
                </tr>
                {product.weight && (
                  <tr>
                    <th>Trng lng</th>
                    <td>{product.weight}g</td>
                  </tr>
                )}
                <tr>
                  <th>Danh mc</th>
                  <td>{product.category_name}</td>
                </tr>
                {product.brand_name && (
                  <tr>
                    <th>Thng hiu</th>
                    <td>{product.brand_name}</td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
          {/* Features */}
          <div className="row mb-4">
            <div className="col-md-4 mb-2">
              <div className="border rounded p-2 d-flex align-items-center gap-2">
                <span className="badge bg-success rounded-circle" style={{width:'32px',height:'32px'}}>1</span>
                <span>Cam kt chnh hng 100%</span>
              </div>
            </div>
            <div className="col-md-4 mb-2">
              <div className="border rounded p-2 d-flex align-items-center gap-2">
                <span className="badge bg-success rounded-circle" style={{width:'32px',height:'32px'}}>2</span>
                <span>Bo hnh theo chnh sch cng ty</span>
              </div>
            </div>
            <div className="col-md-4 mb-2">
              <div className="border rounded p-2 d-flex align-items-center gap-2">
                <span className="badge bg-success rounded-circle" style={{width:'32px',height:'32px'}}>3</span>
                <span>Giao hng nhanh ton quc</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* Product Description */}
      {product.description && (
        <div className="row mt-5">
          <div className="col-12">
            <div className="card">
              <div className="card-header">
                <h5 className="mb-0">
                  <i className="bi bi-file-text me-2"></i>
                  M t sn phm
                </h5>
              </div>
              <div className="card-body">
                <div className="product-description" style={{whiteSpace: 'pre-line'}}>
                  {product.description}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};



export default ProductDetail;
