import React, { useState } from 'react';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

const AdminLayout = ({ children }) => {
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false);
  const { currentUser, logout } = useAuth();
  const location = useLocation();
  const navigate = useNavigate();

  const handleLogout = () => {
    logout();
    navigate('/');
  };

  const menuItems = [
    { path: '/admin', icon: '', label: 'Dashboard' },
    { path: '/admin/products', icon: '', label: 'Qun l sn phm' },
    { path: '/admin/categories', icon: '', label: 'Qun l danh mc' },
    { path: '/admin/brands', icon: '', label: 'Qun l thng hiu' },
    { path: '/admin/orders', icon: '', label: 'Qun l n hng' },
    { path: '/admin/coupons', icon: '', label: 'M gim gi' },
    { path: '/admin/users', icon: '', label: 'Qun l ngi dng' },
    { path: '/admin/contacts', icon: '', label: 'Lin h' },
    { path: '/admin/settings', icon: '', label: 'Ci t' },
  ];

  return (
    <div className="admin-layout">
      {/* Sidebar */}
      <div className={`admin-sidebar ${sidebarCollapsed ? 'collapsed' : ''}`}>
        <div className="sidebar-header">
          <div className="d-flex align-items-center">
            <img src="/images/logo.png" style={{ height: "40px" }} />
            <h3 className="ms-2 mb-0">Badminton Admin</h3>
          </div>
        </div>
        <ul className="sidebar-menu">
          {menuItems.map((item) => (
            <li key={item.path}>
              <Link
                to={item.path}
                className={location.pathname === item.path ? 'active' : ''}
              >
                <i>{item.icon}</i>
                {item.label}
              </Link>
            </li>
          ))}
        </ul>
      </div>

      {/* Main Content */}
      <div className="admin-content">
        {/* Top Bar */}
        <div className="admin-topbar">
          <div className="d-flex justify-content-between align-items-center w-100">
            <div className="d-flex align-items-center">
              <button
                className="btn btn-link d-md-none"
                onClick={() => setSidebarCollapsed(!sidebarCollapsed)}
              >
                
              </button>
              <h4 className="mb-0 ms-2">
                {menuItems.find(item => item.path === location.pathname)?.label || 'Dashboard'}
              </h4>
            </div>
            
            <div className="d-flex align-items-center">
              <div className="dropdown">
                <button
                  className="btn btn-outline-secondary dropdown-toggle"
                  type="button"
                  data-bs-toggle="dropdown"
                >
                   {currentUser?.name}
                </button>
                <ul className="dropdown-menu dropdown-menu-end">
                  <li>
                    <Link className="dropdown-item" to="/">
                       V trang ch
                    </Link>
                  </li>
                  <li><hr className="dropdown-divider" /></li>
                  <li>
                    <button className="dropdown-item" onClick={handleLogout}>
                       ng xut
                    </button>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        {/* Page Content */}
        {children}
      </div>
    </div>
  );
};

export default AdminLayout;
