import React, { useState, useEffect } from 'react';
import AdminLayout from '../../components/AdminLayout';
import axios from 'axios';

const AdminCoupons = () => {
  const [coupons, setCoupons] = useState([]);
  const [showModal, setShowModal] = useState(false);
  const [editingCoupon, setEditingCoupon] = useState(null);
  const [formData, setFormData] = useState({
    code: '',
    name: '',
    description: '',
    type: 'percentage',
    value: '',
    minimum_amount: '',
    maximum_discount: '',
    usage_limit: '',
    user_limit: 1,
    valid_from: '',
    valid_to: '',
    status: 'active'
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchCoupons();
  }, []);

  const fetchCoupons = async () => {
    try {
      const response = await axios.get('/api/coupons/admin');
      setCoupons(response.data);
    } catch (error) {
      console.error('Error fetching coupons:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const dataToSubmit = {
        ...formData,
        code: formData.code.toUpperCase()
      };

      if (editingCoupon) {
        await axios.put(`/api/coupons/${editingCoupon.id}`, dataToSubmit);
        alert('Cp nht m gim gi thnh cng!');
      } else {
        await axios.post('/api/coupons', dataToSubmit);
        alert('Thm m gim gi thnh cng!');
      }
      
      setShowModal(false);
      setEditingCoupon(null);
      resetForm();
      fetchCoupons();
    } catch (error) {
      console.error('Error saving coupon:', error);
      alert('C li xy ra!');
    }
  };

  const handleEdit = (coupon) => {
    setEditingCoupon(coupon);
    setFormData({
      code: coupon.code,
      name: coupon.name,
      description: coupon.description || '',
      type: coupon.type,
      value: coupon.value,
      minimum_amount: coupon.minimum_amount,
      maximum_discount: coupon.maximum_discount || '',
      usage_limit: coupon.usage_limit || '',
      user_limit: coupon.user_limit,
      valid_from: new Date(coupon.valid_from).toISOString().slice(0, 16),
      valid_to: new Date(coupon.valid_to).toISOString().slice(0, 16),
      status: coupon.status
    });
    setShowModal(true);
  };

  const resetForm = () => {
    setFormData({
      code: '',
      name: '',
      description: '',
      type: 'percentage',
      value: '',
      minimum_amount: '',
      maximum_discount: '',
      usage_limit: '',
      user_limit: 1,
      valid_from: '',
      valid_to: '',
      status: 'active'
    });
  };

  const handleModalClose = () => {
    setShowModal(false);
    setEditingCoupon(null);
    resetForm();
  };

  return (
    <AdminLayout>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2> Qun l m gim gi</h2>
        <button 
          className="btn btn-admin-primary"
          onClick={() => setShowModal(true)}
        >
           Thm m gim gi
        </button>
      </div>

      {loading ? (
        <div className="text-center py-5">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      ) : (
        <div className="admin-table">
          <table className="table">
            <thead>
              <tr>
                <th>M</th>
                <th>Tn</th>
                <th>Loi</th>
                <th>Gi tr</th>
                <th> dng</th>
                <th>Hn s dng</th>
                <th>Trng thi</th>
                <th>Thao tc</th>
              </tr>
            </thead>
            <tbody>
              {coupons.map(coupon => (
                <tr key={coupon.id}>
                  <td>
                    <code className="bg-primary text-white px-2 py-1 rounded">
                      {coupon.code}
                    </code>
                  </td>
                  <td>{coupon.name}</td>
                  <td>
                    <span className={`badge ${coupon.type === 'percentage' ? 'bg-success' : 'bg-info'}`}>
                      {coupon.type === 'percentage' ? 'Phn trm' : 'C nh'}
                    </span>
                  </td>
                  <td>
                    {coupon.type === 'percentage' 
                      ? `${coupon.value}%` 
                      : new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(coupon.value)
                    }
                  </td>
                  <td>{coupon.used_count}/{coupon.usage_limit || ''}</td>
                  <td>
                    <small>
                      {new Date(coupon.valid_from).toLocaleDateString('vi-VN')} - {' '}
                      {new Date(coupon.valid_to).toLocaleDateString('vi-VN')}
                    </small>
                  </td>
                  <td>
                    <span className={`badge ${coupon.status === 'active' ? 'bg-success' : 'bg-secondary'}`}>
                      {coupon.status === 'active' ? 'Hot ng' : 'Tm dng'}
                    </span>
                  </td>
                  <td>
                    <button 
                      className="btn btn-sm btn-outline-primary me-2"
                      onClick={() => handleEdit(coupon)}
                    >
                      Sa
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="modal show d-block admin-modal" tabIndex="-1">
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">
                  {editingCoupon ? 'Sa m gim gi' : 'Thm m gim gi'}
                </h5>
                <button 
                  type="button" 
                  className="btn-close" 
                  onClick={handleModalClose}
                ></button>
              </div>
              <form onSubmit={handleSubmit}>
                <div className="modal-body">
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label className="form-label">M gim gi:</label>
                      <input
                        type="text"
                        className="form-control"
                        value={formData.code}
                        onChange={(e) => setFormData({...formData, code: e.target.value.toUpperCase()})}
                        placeholder="VD: WELCOME10"
                        required
                      />
                    </div>
                    
                    <div className="col-md-6 mb-3">
                      <label className="form-label">Tn m:</label>
                      <input
                        type="text"
                        className="form-control"
                        value={formData.name}
                        onChange={(e) => setFormData({...formData, name: e.target.value})}
                        required
                      />
                    </div>
                  </div>
                  
                  <div className="mb-3">
                    <label className="form-label">M t:</label>
                    <textarea
                      className="form-control"
                      rows="2"
                      value={formData.description}
                      onChange={(e) => setFormData({...formData, description: e.target.value})}
                    />
                  </div>
                  
                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label className="form-label">Loi gim gi:</label>
                      <select
                        className="form-select"
                        value={formData.type}
                        onChange={(e) => setFormData({...formData, type: e.target.value})}
                      >
                        <option value="percentage">Phn trm (%)</option>
                        <option value="fixed">C nh (VND)</option>
                      </select>
                    </div>
                    
                    <div className="col-md-6 mb-3">
                      <label className="form-label">Gi tr:</label>
                      <input
                        type="number"
                        className="form-control"
                        value={formData.value}
                        onChange={(e) => setFormData({...formData, value: e.target.value})}
                        placeholder={formData.type === 'percentage' ? '10' : '50000'}
                        required
                      />
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label className="form-label">n hng ti thiu:</label>
                      <input
                        type="number"
                        className="form-control"
                        value={formData.minimum_amount}
                        onChange={(e) => setFormData({...formData, minimum_amount: e.target.value})}
                        placeholder="0"
                      />
                    </div>
                    
                    <div className="col-md-6 mb-3">
                      <label className="form-label">Gim ti a (nu %):</label>
                      <input
                        type="number"
                        className="form-control"
                        value={formData.maximum_discount}
                        onChange={(e) => setFormData({...formData, maximum_discount: e.target.value})}
                        placeholder=" trng nu khng gii hn"
                      />
                    </div>
                  </div>

                  <div className="row">
                    <div className="col-md-6 mb-3">
                      <label className="form-label">T ngy:</label>
                      <input
                        type="datetime-local"
                        className="form-control"
                        value={formData.valid_from}
                        onChange={(e) => setFormData({...formData, valid_from: e.target.value})}
                        required
                      />
                    </div>
                    
                    <div className="col-md-6 mb-3">
                      <label className="form-label">n ngy:</label>
                      <input
                        type="datetime-local"
                        className="form-control"
                        value={formData.valid_to}
                        onChange={(e) => setFormData({...formData, valid_to: e.target.value})}
                        required
                      />
                    </div>
                  </div>
                </div>
                <div className="modal-footer">
                  <button 
                    type="button" 
                    className="btn btn-secondary" 
                    onClick={handleModalClose}
                  >
                    Hy
                  </button>
                  <button type="submit" className="btn btn-primary">
                    {editingCoupon ? 'Cp nht' : 'Thm'}
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
      {showModal && <div className="modal-backdrop show"></div>}
    </AdminLayout>
  );
};

export default AdminCoupons;
