import React, { useState, useEffect } from 'react';
import AdminLayout from '../../components/AdminLayout';
import axios from 'axios';

const AdminSettings = () => {
  const [settings, setSettings] = useState({});
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      const response = await axios.get('/api/settings');
      const settingsObj = {};
      response.data.forEach(setting => {
        settingsObj[setting.key_name] = setting.value;
      });
      setSettings(settingsObj);
    } catch (error) {
      console.error('Error fetching settings:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setSaving(true);
    
    try {
      await axios.put('/api/settings', settings);
      alert('Cp nht ci t thnh cng!');
    } catch (error) {
      console.error('Error saving settings:', error);
      alert('C li xy ra!');
    } finally {
      setSaving(false);
    }
  };

  const handleChange = (key, value) => {
    setSettings({
      ...settings,
      [key]: value
    });
  };

  if (loading) {
    return (
      <AdminLayout>
        <div className="text-center py-5">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      </AdminLayout>
    );
  }

  return (
    <AdminLayout>
      <h2 className="mb-4"> Ci t h thng</h2>

      <form onSubmit={handleSubmit}>
        <div className="row">
          <div className="col-md-8">
            <div className="admin-card">
              <div className="card-header">
                <h5 className="mb-0">Thng tin c bn</h5>
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-md-6 mb-3">
                    <label className="form-label">Tn website:</label>
                    <input
                      type="text"
                      className="form-control"
                      value={settings.site_name || ''}
                      onChange={(e) => handleChange('site_name', e.target.value)}
                    />
                  </div>
                  
                  <div className="col-md-6 mb-3">
                    <label className="form-label">n v tin t:</label>
                    <input
                      type="text"
                      className="form-control"
                      value={settings.currency || 'VND'}
                      onChange={(e) => handleChange('currency', e.target.value)}
                    />
                  </div>
                </div>

                <div className="mb-3">
                  <label className="form-label">M t website:</label>
                  <textarea
                    className="form-control"
                    rows="3"
                    value={settings.site_description || ''}
                    onChange={(e) => handleChange('site_description', e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <label className="form-label">Min ph ship t (VND):</label>
                  <input
                    type="number"
                    className="form-control"
                    value={settings.free_shipping_threshold || ''}
                    onChange={(e) => handleChange('free_shipping_threshold', e.target.value)}
                  />
                </div>
              </div>
            </div>

            <div className="admin-card">
              <div className="card-header">
                <h5 className="mb-0">Thng tin lin h</h5>
              </div>
              <div className="card-body">
                <div className="row">
                  <div className="col-md-6 mb-3">
                    <label className="form-label">S in thoi:</label>
                    <input
                      type="text"
                      className="form-control"
                      value={settings.contact_phone || ''}
                      onChange={(e) => handleChange('contact_phone', e.target.value)}
                    />
                  </div>
                  
                  <div className="col-md-6 mb-3">
                    <label className="form-label">Email lin h:</label>
                    <input
                      type="email"
                      className="form-control"
                      value={settings.contact_email || ''}
                      onChange={(e) => handleChange('contact_email', e.target.value)}
                    />
                  </div>
                </div>

                <div className="mb-3">
                  <label className="form-label">a ch:</label>
                  <textarea
                    className="form-control"
                    rows="3"
                    value={settings.contact_address || ''}
                    onChange={(e) => handleChange('contact_address', e.target.value)}
                  />
                </div>
              </div>
            </div>

            <div className="admin-card">
              <div className="card-header">
                <h5 className="mb-0">Ci t SEO</h5>
              </div>
              <div className="card-body">
                <div className="mb-3">
                  <label className="form-label">Meta Title:</label>
                  <input
                    type="text"
                    className="form-control"
                    value={settings.meta_title || ''}
                    onChange={(e) => handleChange('meta_title', e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <label className="form-label">Meta Description:</label>
                  <textarea
                    className="form-control"
                    rows="3"
                    value={settings.meta_description || ''}
                    onChange={(e) => handleChange('meta_description', e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <label className="form-label">Keywords:</label>
                  <input
                    type="text"
                    className="form-control"
                    value={settings.meta_keywords || ''}
                    onChange={(e) => handleChange('meta_keywords', e.target.value)}
                    placeholder="Cch nhau bng du phy"
                  />
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-4">
            <div className="admin-card">
              <div className="card-header">
                <h5 className="mb-0">Ci t nhanh</h5>
              </div>
              <div className="card-body">
                <div className="mb-3">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      checked={settings.maintenance_mode === '1'}
                      onChange={(e) => handleChange('maintenance_mode', e.target.checked ? '1' : '0')}
                    />
                    <label className="form-check-label">
                      Ch  bo tr
                    </label>
                  </div>
                  <small className="text-muted">Website s hin th trang bo tr</small>
                </div>

                <div className="mb-3">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      checked={settings.allow_registration === '1'}
                      onChange={(e) => handleChange('allow_registration', e.target.checked ? '1' : '0')}
                    />
                    <label className="form-check-label">
                      Cho php ng k
                    </label>
                  </div>
                  <small className="text-muted">Khch hng c th to ti khon mi</small>
                </div>

                <div className="mb-3">
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="checkbox"
                      checked={settings.auto_approve_reviews === '1'}
                      onChange={(e) => handleChange('auto_approve_reviews', e.target.checked ? '1' : '0')}
                    />
                    <label className="form-check-label">
                      T ng duyt nh gi
                    </label>
                  </div>
                  <small className="text-muted">nh gi s hin th ngay khng cn duyt</small>
                </div>
              </div>
            </div>

            <div className="admin-card">
              <div className="card-header">
                <h5 className="mb-0">Thng k</h5>
              </div>
              <div className="card-body">
                <div className="mb-2">
                  <small className="text-muted">Phin bn h thng:</small>
                  <div>v1.0.0</div>
                </div>
                <div className="mb-2">
                  <small className="text-muted">Cp nht cui:</small>
                  <div>{new Date().toLocaleDateString('vi-VN')}</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="d-flex justify-content-end mt-4">
          <button 
            type="button" 
            className="btn btn-secondary me-2"
            onClick={() => window.location.reload()}
          >
            Hy b
          </button>
          <button 
            type="submit" 
            className="btn btn-primary"
            disabled={saving}
          >
            {saving ? 'ang lu...' : 'Lu ci t'}
          </button>
        </div>
      </form>
    </AdminLayout>
  );
};

export default AdminSettings;
